import React from 'react';
import {Panel, Div, Button, Cell, Avatar} from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import VKStories from "vk-stories";
import connect from "@vkontakte/vkui-connect-promise";
 

const fields = [
    {
        x: 540,
        y: 1133,
        value: "Hello World",
        font: "96px Arial",
        align: "center",
        color: "#FFFFFF"
    }
];



//компонент модального окна
class Modal extends React.Component {
  constructor(props) {
      super(props);
  }


  GenerateStory = () =>{
  VKStories.generateStoryFromTemplate(require("../img/forest.png"), fields)

    .then((story) => {
      VKStories.init(connect);
    VKStories.shareStory(7150376, story, { add_to_news: true });
      this.props.theStory();
        console.log("работает")
    })
    .catch(console.error);
  }

  render() {
    return(
      <Div>
       <Avatar src={this.props.fetchedUser.photo_200} size={150} />
       <Cell id="name">{this.props.fetchedUser.first_name+" "+this.props.fetchedUser.last_name}</Cell>
      <Button size="xl" onClick={this.GenerateStory} style={{"background":"linear-gradient(to right #4d6cb7 #182848)", "color": "#fff"}}>Поделиться</Button>
      </Div>
   );
  }

}

export default Modal;